<template>
    <section data-v-portal>
        <!-- <div id="appTitle" data-v-portal>{{appTitle}}</div> -->
        <!--  -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 
        <div class='entry' data-v-portal>
            <h1 data-v-portal>{{welcomeMsg}} {{appTitle}}</h1>
            <p data-v-portal>Hello {{userName}} !</p>
            <p data-v-portal>Today is {{today}}.</p>
        </div>

        <FAbalanceRep />

    </section>
</template>

<script>
import ViewHeader   from '../components/ViewHeader'
import FAbalanceRep from '../components/FaBalanceRep'

export default {
    name : 'Portal',

    props: ['userName'],
    // ------------------------------------
    //  
    // ------------------------------------
    components : {
        ViewHeader,
        FAbalanceRep
    },

    // ------------------------------------
    //  
    // ------------------------------------
    created(){
        let today = new Date()
        this.today = today.getFullYear() + "/" +  Number(today.getMonth() + 1) + "/"+ today.getDate()
    },

    data(){
        return{
            moduleName : "aliss",
            viewName : "Portal",
            welcomeMsg : "Welcome to",
            appTitle : "aliss WEB Suite",

            today : ""
        }
    }
}
</script>

<style scoped>
    /* section[data-v-portal]{
        height:250px;
    } */

    p[data-v-portal]
    {
        margin : 10px 20px;
    }

    h1[data-v-portal]{
        margin : 5px 10px;
        font-size: 120%;
        font-weight: 400;
        color: #f16272;
    }

    .entry[data-v-portal]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .portalReport[data-v-portal]{
        width:95%;
        margin: 0 auto;
    }

    table, th,td, tr[data-v-portal]{
        text-align: left;
        padding: 5px 10px;
    }


    #appTitle[data-v-portal]{
        font-size:400%;
        font-family: 'Didact Gothic';
        text-align: center;
        color: #f16272;

        padding :10% 0px;
    }
</style>



