<template>
    <div class='entry' data-v-balanceRep>
        <h1 data-v-balanceRep></h1>
        <div class="portalReport" data-v-balanceRep>
            <table width='100%' data-v-balanceRep>
                <tr>
                    <th width='20%'></th>
                    <th width='20%'></th>
                    <th width='60%'></th>
                </tr>
                <tr v-for='(balance, index) in balances' :key='index'>
                    <td>{{ balance.name }}</td>
                    <td>{{ balance.currentYear }}.{{ balance.currentPeriod }}</td>
                    <td>{{ balance.balance }}</td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

export default {
    name : 'FAbalanceRep',
    mixins : [apiUrls],

    data(){
        return{
            balances : [
                {
                    id : null,
                    name : "",
                    balance : null,
                    currentYear : null,
                    currentPeriod : null
                }
            ]
        }
    },

    //--------------------------------------
    // 
    //--------------------------------------
    created(){
        // 
        let token = Cookies.get('token')
        if(token == null)
        {
            return
        }

        // this
        const self = this

        // BankAPI call
        axios.get(this.apiUrls["getBanks"],
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                return
            }
            
            // 
            for(let i=0; i<response.data.data.length; i++)
            {
                // 
                let data = Object.assign({}, self.balances[0])
                // 
                data.id             = response.data.data[i].id
                data.name           = response.data.data[i].name
                data.balance        = Number(response.data.data[i].balance).toLocaleString( undefined, { maximumFractionDigits: 20 })
                // data.balance        = response.data.data[i].balance
                data.currentYear    = response.data.data[i].currentYear
                data.currentPeriod  = response.data.data[i].currentPeriod

                self.balances.push(data)
            }
            
            self.balances.shift()
        })
        .catch(function(error){
            return
        })
    }
}
</script>

<style scoped>
    p[data-v-balanceRep]
    {
        margin : 10px 20px;
    }

    h1[data-v-balanceRep]{
        margin : 5px 10px;
        font-size: 120%;
        font-weight: 400;
        color: #f16272;
    }

    .entry[data-v-balanceRep]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .portalReport[data-v-balanceRep]{
        width:95%;
        margin: 0 auto;
    }

    table, th,td, tr[data-v-balanceRep]{
        text-align: left;
        padding: 5px 10px;
    }


    #appTitle[data-v-balanceRep]{
        font-size:400%;
        font-family: 'Didact Gothic';
        text-align: center;
        color: #f16272;

        padding :10% 0px;
    }
</style>




